$zero:0;
* {
    padding: $zero;
    margin: $zero
}

body {
    background-color: #f4f4f4;
    font-size: 14px;
    color: #666
}

ul {
    list-style: none
}

.header {
    width: 1190px;
    height: 200px;
    background-color: orange;
    margin: 0 auto 10px
}

.nav {
    width: 100%;
    height: 60px;
    background-color: #abcdef
}

.banner {
    width: 1190px;
    margin: 0 auto
}

.menu {
    position: relative;
    z-index: 3
}

.menu ul {
    overflow: hidden;
    padding: 10px 0;
    height: 450px;
    background-color: #fefefe;
    color: #636363;
    margin-top: 10px;
    width: 190px
}

.menu ul li {
    height: 25px;
    line-height: 25px;
    padding-left: 18px
}

.menu ul li.active {
    background-color: #d9d9d9
}

.menu li a {
    font-size: 14px;
    color: #333;
    text-decoration: none
}

.menu li span {
    padding: 0 2px;
    font-size: 12px
}

.cartlist {
    position: absolute;
    left: 191px;
    top: 0;
    width: 998px;
    min-height: 468px;
    border: 1px solid #f7f7f7;
    background-color: #fff;
    box-shadow: 2px 0 5px rgba(0, 0, 0, .3);
    display: none;
    transition: top .3s ease
}

.item {
    padding: 20px;
    display: none
}